import Navbar from "@/components/Navbar";
import Hero from "@/components/Hero";
import Portfolio from "@/components/Portfolio";

export default function Home() {
    return (
        <main className="min-h-screen">
            <Navbar />
            <Hero />
            <Portfolio />

            {/* Basic Services Section for now */}
            <section id="services" className="py-24 px-6 bg-gray-50 dark:bg-zinc-900">
                <div className="max-w-7xl mx-auto">
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-12 text-center md:text-left">
                        <div>
                            <h3 className="text-2xl font-bold mb-4">Görsel Prodüksiyon</h3>
                            <p className="text-gray-500">Sinematik video ve high-end fotoğraf çekimleri ile hikayenizi en güçlü şekilde anlatıyoruz.</p>
                        </div>
                        <div>
                            <h3 className="text-2xl font-bold mb-4">Dijital Strateji</h3>
                            <p className="text-gray-500">Modern mecralarda markanızı öne çıkaracak veri odaklı içerik stratejileri geliştiriyoruz.</p>
                        </div>
                        <div>
                            <h3 className="text-2xl font-bold mb-4">Kreatif Marka Kimliği</h3>
                            <p className="text-gray-500">Logodan tipografiye, markanızın tüm görsel dilini baştan aşağı tasarlıyoruz.</p>
                        </div>
                    </div>
                </div>
            </section>

            {/* Basic Contact Section for now */}
            <section id="contact" className="py-32 px-6 bg-black text-white text-center">
                <h2 className="text-5xl md:text-7xl font-black mb-8">BİZİMLE ÇALIŞIN</h2>
                <p className="text-xl text-gray-400 mb-12">Yenilikçi fikirlerinizi gerçeğe dönüştürelim.</p>
                <button className="px-12 py-5 bg-white text-black font-bold text-lg hover:bg-gray-200 transition-colors">
                    İLETİŞİME GEÇİN
                </button>
                <footer className="mt-32 pt-16 border-t border-white/10 text-gray-500 flex flex-col md:flex-row justify-between items-center gap-4">
                    <p>© 2026 ALTUNMEDYA. Tüm hakları saklıdır.</p>
                    <div className="flex gap-8">
                        <a href="#" className="hover:text-white transition-colors">Instagram</a>
                        <a href="#" className="hover:text-white transition-colors">Behance</a>
                        <a href="#" className="hover:text-white transition-colors">LinkedIn</a>
                    </div>
                </footer>
            </section>
        </main>
    );
}
